.myBtn {
    color: white;
    border: 1px solid #390452;
    border-radius: 5px;
    background: #390452;
    padding: 10px 20px;
    margin: 1em 0;
    cursor: pointer;
    font-family: "Comic Sans MS";
    transition: background-color 0.4s;
}

.myBtn:hover {
    background: #970ce1;
}